  public authDetails: any;
  public helpMenuId = 0;
  public LogoutAdminMenuId = 0;
  public SideMenuList: any[] = [];
  public isConsultUser: boolean =
    localStorage.authDetails != undefined &&
    (JSON.parse(localStorage.authDetails).UserTypeId == EnmUserType.Consult ||
      JSON.parse(localStorage.authDetails).UserTypeId ==
        EnmUserType.ConsultAdmin)
      ? true
      : false;
  public isClinicBko: boolean;
